body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.addTaskForm {
  font-size: 20px;
  color: white;
}

.addTaskForm input {
  margin: 10px;
  padding: 5px 10px;
  font-size: 20px
}

.addTaskForm button {
  background-color: rgb(29, 211, 29);
  height: 40px;
  width: 100px;
  border: none;
  border-radius: 10px;
  display: inline-block;
  color: white;
  font-size: 25px;
  line-height: 40px;
  text-align: center;
  cursor: pointer;
}
 .task {
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: flex-start;
     text-align: center;
     position: relative;
     flex-wrap: wrap;
     border-bottom: 3px solid white;
     margin-bottom: 8px;
     padding: 6px 0;
 }

 .task p {
     font-size: 25px;
     word-wrap: break-word;
     max-width: 230px;
     height: auto;
 }

 .task span.date {
     position: absolute;
     right: 150px;
     font-size: 12px;
 }

 .task button.doneTask,
 .task button.deleteTask {
     position: absolute;
     right: 60px;
     background-color: transparent;
     border: none;
     color: rgb(29, 211, 29);
     font-size: 20px;
     cursor: pointer;
 }

 .task button.deleteTask {
     position: absolute;
     right: 0;
     color: red;

 }

 .done {
     position: relative;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     text-align: center;
     color: white;
     margin-bottom: 10px;
 }

.done p {
     width: 550px;
     word-break: break-word;
 }

 .done strong {
     font-weight: normal;
     font-size: 20px;
 }

 .done span {
     font-size: 15px;
 }

 .done button {
     position: absolute;
     right: 50px;
     top: 20%;
     width: 30px;
     height: 30px;
     font-size: 25px;
     background-color: transparent;
     color: red;
     border: 1px solid red;
     border-radius: 50%;
     cursor: pointer;
 }
.activeTasks {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;

}

.activeTasks h2 {
    font-size: 30px;
    margin: 20px 0;
}

.activeTasks h3 {
    font-size: 20px;
    font-weight: normal;
    letter-spacing: 1px;
}

.activeTasks div {
    width: 500px;
}


